/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: #333;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	font-size: 1rem;
	line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
}

p {
	margin-bottom: 1.5em;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/* Elements
--------------------------------------------- */
body {
	background: #fff;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: none;
}

ol {
	list-style: none;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
	border-collapse: collapse;
	text-align: left;
}

table th {
	background-color: #ddd;
	color: #000000;
	padding: 30px;
}

table td {
	background-color: #f2f2f2;
	padding: 30px;
	border-bottom: 1px solid #ccc;
}

table tr:nth-child(even) td {
	background-color: #ddd;
}

.schedule th, .schedule td {
	display: block;
}

.schedule th {
	text-align: left;
}

@media only screen and (min-width: 600px) {
	.schedule th, .schedule td {
		display: table-cell;
		width: 43%;
		padding: 2rem;
	}
}
/* Links
--------------------------------------------- */
a {
	color: #111;
	list-style: none;
}
a:hover, a:focus {
	color: #4169e1;
}
a:focus {
	outline: thin dotted;
}
a:hover, a:active {
	outline: 0;
}

.site-footer a {
	color: #fff;
}

.student-content .wp-block-button a {
	color: #fff;
}

.site-header .site-title a {
	color: #fff;
	text-decoration: none;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	line-height: 1;
	padding: 0.6em 1em 0.4em;
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover {
	border-color: #ccc #bbb #aaa;
}
button:active, button:focus,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:active,
input[type=submit]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
.site {
	max-width: 2200px;
	margin: 0 auto;
	overflow: hidden;
	display: grid;
	grid-template-columns: 1fr minmax(0, 1000px) 1fr;
	grid-template-areas: "header header header" ". main ." ". sidebar ." "footer footer footer";
}

.site-header {
	display: grid;
	grid-area: header;
	grid-column: 2/3;
}

.site-main {
	grid-area: main;
	margin-top: 55px;
	padding: 0 1rem;
}

.widget-area {
	grid-area: sidebar;
}

.recent-posts {
	display: flex;
	flex-direction: row;
	text-align: center;
	list-style: none;
}

.recent-posts div {
	margin: 1rem;
}

.no-sidebar .site {
	display: grid;
	grid-template-columns: auto;
	grid-template-areas: "header" "main" "footer";
}

/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
.main-navigation {
	display: block;
	width: 100%;
	position: absolute;
	top: 52px;
	right: 0;
	z-index: 1000;
}
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
	background-color: black;
}
.main-navigation ul ul {
	width: 100%;
}
@media screen and (min-width: 50em) {
	.main-navigation ul ul {
		box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
		float: left;
		position: absolute;
		top: 100%;
		left: -999em;
		z-index: 99999;
		flex-direction: column;
	}
}
.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}
.main-navigation ul ul li {
	display: flex;
	justify-content: center;
}
@media screen and (min-width: 50em) {
	.main-navigation ul ul li {
		justify-content: left;
	}
	.main-navigation ul ul li:hover > ul, .main-navigation ul ul li.focus > ul {
		display: block;
		left: auto;
	}
}
.main-navigation ul ul a {
	width: 200px;
}
.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}
.main-navigation li {
	position: relative;
}
.main-navigation a {
	padding: 1rem;
	display: block;
	font-size: 0.9em;
	text-decoration: none;
	text-align: center;
	font-weight: 700;
	color: white;
	text-transform: uppercase;
}
.main-navigation .menu-menu-1-container .menu li ul li {
	background-color: #666;
}
.main-navigation .menu-menu-1-container .menu li ul li :hover {
	background-color: #003c71;
	width: 100%;
}
.main-navigation .menu-menu-1-container .menu li ul li :focus {
	background-color: #003c71;
}
.main-navigation .menu-menu-1-container .menu li ul li a {
	color: white;
}
@media screen and (min-width: 50em) {
	.main-navigation .menu-menu-1-container .menu li ul li {
		background-color: black;
	}
}
.main-navigation .menu-menu-1-container .menu > li:hover {
	background-color: #003c71;
}
.main-navigation .menu-menu-1-container a {
	color: white;
}
.main-navigation .current-menu-item {
	background-color: #003c71;
}
/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

.menu-toggle {
	display: block;
	width: 46px;
	height: 46px;
	margin: 0;
	padding: 0;
	font-size: 0.9em;
	color: #fff;
	position: absolute;
	right: 1rem;
	z-index: 1100000;
	top: -3rem;
	border: none;
	box-shadow: none;
	text-shadow: none;
	text-decoration: none;
	cursor: pointer;
	background: transparent;
}
.menu-toggle svg {
	color: #fff;
}

@media screen and (min-width: 50em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: flex;
		justify-content: center;
	}
}
.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
}
.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
}
.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}
.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	text-align: end;
	flex: 1 0 50%;
}

.site-header {
	width: 100%;
	height: 55px;
	background-color: #000000;
	border-top: 3px solid #305c9f;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1000;
}

@media screen and (min-width: 50em) {
	.site-header {
		display: grid;
		grid-template-columns: 1fr minmax(auto, 12.5rem) minmax(auto, 50rem) 1fr;
		align-items: center;
	}
}
.site-header {
	grid-area: header;
}

.site-header .site-title {
	width: 200px;
	font-size: 1.3em;
	text-transform: uppercase;
	margin: 0.4rem 1rem 0;
}

@media screen and (min-width: 50em) {
	.main-navigation {
		width: auto;
		position: relative;
		top: 0;
		align-self: center;
		margin-left: auto;
		height: 52px;
		line-height: 52px;
	}
	.main-navigation a {
		padding: 0 1rem;
	}
}
@media screen and (min-width: 50em) {
	.site-header .site-branding {
		grid-column: 2/3;
		margin: 0 1rem 0;
	}
}
.footer-navigation {
	align-items: left;
}

.site-logo img {
	max-width: 150px !important;
}

/* Posts and pages
--------------------------------------------- */
.sticky {
	display: block;
}

.post,
.page {
	margin: 0 0 1.5em;
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.staff-wrapper {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: space-between;
	margin-bottom: 2rem;
}

.staff-item {
	width: 48%;
	margin: 0;
}

.middle-wrapper {
	display: flex;
	flex-direction: row;
	text-align: center;
}

.middle-content {
	display: flex;
	flex-direction: column;
	margin: 2rem;
}

.site-main a {
	color: #003c71;
	text-decoration: none;
	font-weight: 700;
}

.students {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}

.student-entry {
	max-width: 300px;
	margin: auto;
}
@media only screen and (min-width: 600px) {
	.student-entry {
		width: 48%;
		margin: 0 0 3rem;
	}
}
@media only screen and (min-width: 900px) {
	.student-entry {
		width: 30%;
	}
}

.student-content {
	margin: 1.5em 0 0;
}

body {
	color: #444;
	font-family: "Open Sans", Helvetica, Arial, sans-serif;
	font-weight: 300;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.8;
}

@media only screen and (min-width: 600px) {
	.single-taze-student .wp-post-image {
		display: inline;
		float: right;
		margin-left: 1.5em;
		margin-bottom: 0;
	}
}
.tax-taze-student-category .student-thumbnail {
	display: inline;
	float: left;
	margin-right: 1.5em;
}
.tax-taze-student-category article {
	padding-top: 1em;
	clear: both;
}

/* Comments
--------------------------------------------- */
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/* Widgets
--------------------------------------------- */
.widget {
	margin: 0 0 1.5em;
}
.widget select {
	max-width: 100%;
}

.widget-area {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.widget-area section {
	margin: 1rem;
}

.site-info a {
	color: #fff;
}

.footer-content {
	display: flex;
	flex-direction: column;
	text-align: center;
	margin-right: 3rem;
}

.footer-navigation {
	display: flex;
	flex-direction: column;
}

.menu-footer-menu-container ul {
	padding: 0 !important;
	margin: 0 !important;
}

.site-footer {
	grid-area: footer;
	background-color: #333;
	margin-top: 20px;
	padding: 1rem;
	font-size: 0.875rem;
	color: #fff;
	text-align: center;
}
@media screen and (min-width: 62.5em) {
	.site-footer {
		padding: 2rem 1rem;
		text-align: left;
		display: grid;
		grid-template-columns: 1fr repeat(3, minmax(auto, 20.83rem)) 1fr;
	}
}
.site-footer .site-logo {
	grid-column: 2/3;
	margin: 0;
}
.site-footer .footer-navigation {
	display: flex;
	flex-direction: column;
	align-items: center;
}

@media only screen and (min-width: 800px) {
	.entry-footer span {
		display: inline-block;
		margin-right: 3rem;
	}
	.entry-footer span a {
		text-decoration: none;
	}
}
/* Media
--------------------------------------------- */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

.post a.post-thumbnail img {
	max-height: 400px;
	object-fit: cover;
}

/* For Wide alignment */
.image-wide img {
	height: auto;
	width: 100%;
}

.image-wide {
	max-width: 1300px;
	margin-left: -150px;
	margin-right: -150px;
}

.image-alignwide img {
	display: flex;
	justify-content: center;
	align-content: center;
}

.image-full {
	display: block;
	width: 100vw;
	max-width: 100vw;
	margin: 2rem calc(50% - 50vw);
}

img .image-alignfull {
	height: auto;
	width: 100%;
}

/* Captions
--------------------------------------------- */
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}
.wp-caption img[class*=wp-image-] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/* Galleries
--------------------------------------------- */
.gallery {
	margin-bottom: 1.5em;
	display: grid;
	grid-gap: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	width: 100%;
}

.gallery-columns-2 {
	grid-template-columns: repeat(2, 1fr);
}

.gallery-columns-3 {
	grid-template-columns: repeat(3, 1fr);
}

.gallery-columns-4 {
	grid-template-columns: repeat(4, 1fr);
}

.gallery-columns-5 {
	grid-template-columns: repeat(5, 1fr);
}

.gallery-columns-6 {
	grid-template-columns: repeat(6, 1fr);
}

.gallery-columns-7 {
	grid-template-columns: repeat(7, 1fr);
}

.gallery-columns-8 {
	grid-template-columns: repeat(8, 1fr);
}

.gallery-columns-9 {
	grid-template-columns: repeat(9, 1fr);
}

.gallery-caption {
	display: block;
}

/*--------------------------------------------------------------
# Plugins
--------------------------------------------------------------*/
/* Jetpack infinite scroll
--------------------------------------------- */
/* Hide the Posts Navigation and the Footer when Infinite Scroll is in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	display: none;
}

/* Re-display the Theme Footer when Infinite Scroll has reached its end. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Utilities
--------------------------------------------------------------*/
/* Accessibility
--------------------------------------------- */
/* Text meant only for screen readers. */
.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important;
}
.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	clip-path: none;
	color: #21759b;
	display: block;
	font-size: 0.875rem;
	font-weight: 700;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
}

/* Do not show the outline on the skip link target. */
#primary[tabindex="-1"]:focus {
	outline: 0;
}

/* Alignments
--------------------------------------------- */
.alignleft {
	/*rtl:ignore*/
	float: left;
	/*rtl:ignore*/
	margin-right: 1.5em;
	margin-bottom: 1.5em;
}

.alignright {
	/*rtl:ignore*/
	float: right;
	/*rtl:ignore*/
	margin-left: 1.5em;
	margin-bottom: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 1.5em;
}